body {
  background-color: #337435; /* Green background */
}
.auth-page {
    max-width: 400px;
    margin: 50px auto;
    padding: 20px;
    border: 1px solid #ddd;
    background-color: #ffffff;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    text-align: center;
  }

  .auth-page h2 {
    margin-bottom: 20px;
    font-size: 24px;
  }
  
  .auth-page form {
    display: flex;
    flex-direction: column;
  }
  
  .auth-page label {
    text-align: left;
    margin-bottom: 5px;
    font-weight: bold;
  }
  
  .auth-page input {
    padding: 10px;
    margin-bottom: 15px;
    border-radius: 4px;
    border: 1px solid #000000;
    font-size: 16px;
  }
  
  .auth-button {
    background-color: #4CAF50;
    color: white;
    padding: 10px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 16px;
  }
  
  .auth-button:hover {
    background-color: #45a049;
  }
  
  .toggle-button {
    background: none;
    border: none;
    color: #0066cc;
    cursor: pointer;
    font-size: 14px;
    margin-top: 10px;
  }
  
  .toggle-button:hover {
    text-decoration: underline;
}